import React, { Component } from 'react'
import {  Col, Row } from 'reactstrap'
import PropTypes from 'prop-types'
import Highlight from '../../HOC/Highlight';

export default class AboutLearning extends Component {
    render() {
        const { header, items, str, imgUrl } = this.props
        return (
            <Row className="mt-5 ">
                {/* <Col md="4" sm="12">
                    <img className="img-fluid" alt="Котэ" src={imgUrl}></img>
                </Col> */}
                <Col>
                    <Highlight>{header}</Highlight>
                    {Array.isArray(items) && <ul className="mt-3">{items.map((val,i)=> <li key={i}>{val}</li>)}</ul>}
                    <Highlight MainType='h5'>{str}</Highlight>
                </Col>
            </Row>   
        )
    }
}
AboutLearning.defaultProps = {
    imgUrl:'https://psv4.userapi.com/c538304/u240231632/docs/65cf6c04e0df/just_about_vk.gif?extra=jSPxOr5pb_6G_zfpKiZSf8epA8laT7PhfL5cChcok7i-VwDFxrGg2I8vLbMXup8nTVLDoNrtvBJaXYbE6414xS8_8mXXEzP2gRFI-7vsTZgbQwJwF3HDgeNAL2SfsAvb6_HVVmkVkwftTAeRpw-66FQ2'
}

AboutLearning.propTypes = {
    imgUrl: PropTypes.string.isRequired,
    header: PropTypes.string.isRequired,
}


