import React, { Component } from 'react'
import { ListGroup } from 'reactstrap'

import ListItem from '../../containers/BlogFather/ListItem';

export default class List extends Component {

    state = {
        stories: this.props.stories
    }
    
    componentDidUpdate(prevProps){
        if(prevProps.stories !== this.props.stories){
            this.setState({stories: this.props.stories})
        }
    }

    render() {
        const {stories} = this.state
        
        if(Array.isArray(stories)) {

            return (
                <ListGroup className="mb-3">
                    {stories && stories.map((val, i) => <ListItem type={this.props.type} key={i} id={parseInt(val.id)} name={val.name} likes={val.likes}/>)}
                </ListGroup>
            )
        }else{
            return null
        }
    }
}
