import React, { Component } from 'react'
import { Container, Row, Col } from 'reactstrap'

import { Link } from "react-router-dom"

export default class Pagination extends Component {
    render() {
        const { current, count, type } = this.props 
        
        if ((parseInt(current) <= parseInt(count)) && (parseInt(current) > 0)) {
            return             <Container>
                <Row className="text-center mt-3">
                    <Col sm={{ size: 3, offset: 4 }}>
                        <nav>
                            <ul className="pagination pagination justify-content-center">
                                {current > 2 ? <First type={type} /> : null}
                                <Previous type={type} disabled={current < 2 ? true : false} number={current} />
                                {
                                    current > 3 ? <Number type={type} active={false} number={current - 1} /> : null
                                }
                                <Number type={type} active={true} number={current} />
                                {
                                    current < (count - 2) ? <Number type={type} active={false} number={parseInt(current) + 1} /> : null
                                }
                                <Next type={type} disabled={current < count ? false : true} number={current} />
                                {current < (count - 1) ? <Last type={type} count={count} /> : null}
                            </ul>
                        </nav>
                    </Col>
                </Row>
            </Container>
        }else {
            return null
        }
        
    }
}

const First = ({type}) => (
    <li className="page-item">
        <Link to={`/${type}/1`} className="page-link" aria-label="First">
            <span aria-hidden="true">«</span>
            <span className="sr-only">First</span>
        </Link>
    </li>
)

const Previous = ({disabled, number, type}) => (
    <li className={disabled ? "page-item disabled" : "page-item"}>
        <Link to={disabled ? "#" : `/${type}/${number-1}`} className="page-link" aria-label="Previous">
            <span aria-hidden="true">‹</span>
            <span className="sr-only">Previous</span>
        </Link>
    </li>
)

const Number = ({number, active, type}) => (
    <li className={active ? "page-item active" : "page-item"}>
        <Link to={`/${type}/`+ number} className="page-link" aria-label={number}>
            <span aria-hidden="true">{number}</span>
            <span className="sr-only">{number}</span>
        </Link>
    </li>
)

const Next = ({ disabled, number, type }) => (
    <li className={disabled ? "page-item disabled" : "page-item"}>
        <Link to={disabled ? "#" : `/${type}/${parseInt(number) + 1}`} className="page-link" aria-label="Next">
            <span aria-hidden="true">›</span>
            <span className="sr-only">Next</span>
        </Link>
    </li>
)

const Last = ({count, type}) => (
    <li className="page-item">
        <Link to={`/${type}/${count}`} className="page-link" aria-label="Last">
            <span aria-hidden="true">»</span>
            <span className="sr-only">Last</span>
        </Link>
    </li>
)


Pagination.defaultProps = {
    current: 1  
}

