import React, { Component } from 'react'
import { Row, Col } from 'reactstrap'
import Highlight from '../../HOC/Highlight';

export default class Head extends Component {
    render() {
        const { name, position, aboutMe, photo } = this.props
        return (
            <Row className="text-center">
                <Col md="4" >
                    <img style={{ height: 'auto', width: '100%', borderRadius: '100%' }} src={photo} alt="Моя фотография" />
                </Col>
                <Col md={{ offset: '1', size: 7 }}>
                    <Highlight>{name}</Highlight>
                    <p>{position}</p>
                    <Highlight MainType="h3">{position}</Highlight>
                    <div>{aboutMe}</div>
                </Col>
            </Row>

        )
    }
}
