import React, { Component } from 'react'
import {Container} from 'reactstrap'
import { connect } from 'react-redux'
import { fetchBlog } from '../../redux/actions/blog';

import List from './List';
import CustomSpinner from '../../containers/utils/Spinner';

class BlogFather extends Component {

    componentDidMount() {
        this.props.fetchBlog(true, this.props.type)
    }

    componentDidUpdate(prevProps) {
        if (this.props.type !== prevProps.type)
            this.props.fetchBlog(true, this.props.type)
    }


    render() {
        if (this.props.hasError) {
            return <p> OOoooops... We have some troubles</p>
        }

        if (this.props.isLoading && !this.props.data) {
            return <CustomSpinner />
        }

        if (this.props.data) {
            const { blogString, items } = this.props.data 

            return (
                <Container className="mt-5">
                    <h4 className="text-center" style={{ color: '#4E706F'}}>{blogString}</h4>
                    <Container>
                        <List type={this.props.type === 'blogss' ? 'blog' : 'extracurricular'} stories={items}/>
                    </Container>
                </Container>
            )
        }
    }
}


BlogFather.defaultProps = {
    data: []
}

const mapStateToProps = (state, ownProps) => ({
    isLoading: state.blog.isLoading,
    hasError: state.blog.hasError,
    data: state.blog.data
})

const mapDispatchToProps = (dispatch) => ({
    fetchBlog: (short, id) => dispatch(fetchBlog(short, id))
})


export default connect(mapStateToProps, mapDispatchToProps)(BlogFather)   