import { MAINPAGE_FETCH, MAINPAGE_FETCH_SUCCESS, MAINPAGE_FETCH_FAILED } from "../actionType/fetchState";

export const isBlogFetching = (bool, endpoint) => ({
    type: MAINPAGE_FETCH ,
    isLoading: bool,
    endpoint
})

export const fetchBlogSuccess = (payload, endpoint) => ({
    type: MAINPAGE_FETCH_SUCCESS,
    payload, 
    endpoint
})

export const fetchHasErrored = (bool, endpoint) => ({
    type: MAINPAGE_FETCH_FAILED,
    hasError: bool,
    endpoint
})

export const fetchState = (endpoint) => {
    return (dispatch) => {
        dispatch(isBlogFetching(true, endpoint))

        const url =`https://5c86616fcc034a0014bd2569.mockapi.io/api/v1/${endpoint}`

        fetch(url)
            .then((response) => {
                if (!response.ok) {
                    throw Error(response.statusText)
                }
                dispatch(isBlogFetching(false, endpoint))
                return response
            })
            .then((res) => res.json())
            .then((items) => dispatch(fetchBlogSuccess(items, endpoint)))
            .catch((err) => dispatch(fetchHasErrored(true, endpoint)))
    }
}