import React, { Component } from 'react'
import { Container } from 'reactstrap'
import NavigationFooter from '../../containers/Footer/NavigationFooter';

export default class Footer extends Component {
    render() {
        return (
            <div className="page-footer">
                <Container>
                    <NavigationFooter/>
                </Container>
            </div>
        )
    }
}
