import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Container, Col, Row } from 'reactstrap'

import { fetchState } from '../../redux/actions/fetchState';
import CustomSpinner from '../../containers/utils/Spinner';
import  ReactMarkdown  from 'react-markdown'

class Teachers extends Component {

    componentDidMount() {
        this.props.fetchState('teachers')
    }

    // componentDidUpdate(prevProps) {
    //     if (this.props.match.params.id !== prevProps.match.params.id) {
    //         this.props.fetchBlog(false, this.props.match.params.id)
    //     }
    // }

    render() {
        console.log(this.props.match.params[0])
        if (this.props.hasError) {
            return <p> OOoooops... We have some troubles</p>
        }

        if (this.props.isLoading && this.props.data.length === 0) {
            return <CustomSpinner />
        }
        if (this.props.data) {
        return (
            <React.Fragment>
                <Container className="mt-2">
                    <ReactMarkdown className="markdown-body" source={this.props.data && this.props.data[this.props.match.params[0]]} />
                </Container>
            </React.Fragment>)
        }else{ return null}

    }
}

Teachers.defaultProps = {
    data: []
}

const mapStateToProps = (state, ownProps) => ({
    isLoading: state.fetchedState.teachers.isLoading,
    hasError: state.fetchedState.teachers.hasError,
    data: state.fetchedState.teachers.data
})

const mapDispatchToProps = (dispatch) => ({
    fetchState: (endpoint) => dispatch(fetchState(endpoint))
})


export default connect(mapStateToProps, mapDispatchToProps)(Teachers)