import React, { Component } from 'react'
import { connect } from 'react-redux'

import Pagination from '../../containers/Blog/Pagination';
import { fetchBlog } from '../../redux/actions/blog';
import BlogView from '../../containers/Blog/BlogView';
import CustomSpinner from '../../containers/utils/Spinner';
import Article from './Disqus';

class Blog extends Component {

    componentDidMount(){
        this.props.fetchBlog(false, this.props.type, this.props.match.params.id)
    }

    componentDidUpdate(prevProps){
        if (this.props.match.params.id !== prevProps.match.params.id || this.props.type !== prevProps.type ){
            this.props.fetchBlog(false, this.props.type, this.props.match.params.id)
        }
    }

    render() {
        if (this.props.hasError) {
            return <p> OOoooops... We have some troubles</p>
        }
        
        if (this.props.isLoading && this.props.data.length === 0) {
            return <CustomSpinner />
        }

        const { params } = this.props.match

        if (this.props.isLoading && this.props.data && this.props.data.count) {
            return <React.Fragment>
                <CustomSpinner/>
                <Pagination type={this.props.type === 'blogss' ? 'blog' : 'extracurricular'} current={params.id} count={this.props.data.count} />
            </React.Fragment>
        }
        
        return ( 
            <React.Fragment>
                {this.props.data.items && <BlogView data={this.props.data.items}/>}
                <Pagination type={this.props.type === 'blogss' ? 'blog' : 'extracurricular'} current={params.id} count={this.props.data.count} />
                
                <Article body={this.props.data.commentBody } url={this.props.type === 'blogss' ? `https://nashblog.github.io/blog/${params.id}` : `https://nashblog.github.io/extracurricular/${params.id}`} id={`nashblog-${this.props.type}-${params.id}`}/>
            </React.Fragment>)
            
    }
}

Blog.defaultProps ={
    data: []
}

const mapStateToProps = (state, ownProps) => ({
    isLoading: state.blog.isLoading,
    hasError: state.blog.hasError,
    data: state.blog.data
})

const mapDispatchToProps = (dispatch)=>  ({
    fetchBlog: (short, id, type) => dispatch(fetchBlog(short, id, type)) 
})


export default connect(mapStateToProps, mapDispatchToProps)(Blog)