import { BLOG_FETCH, BLOG_FETCH_SUCCESS, BLOG_FETCH_FAILED } from "../actionType/blog";

export const isBlogFetching = (bool) => ({
    type: BLOG_FETCH,
    isLoading: bool
})

export const fetchBlogSuccess = (payload) => ({
    type: BLOG_FETCH_SUCCESS,
    payload
})

export const fetchHasErrored = (bool) => ({
    type: BLOG_FETCH_FAILED,
    hasError: bool
})

export const fetchBlog = (short,type, id ) => {
    return (dispatch) => {
        dispatch(isBlogFetching(true))
        // blogss -- blog
        // 
        const url = short ? `https://5c86616fcc034a0014bd2569.mockapi.io/api/v1/${type}?l=10&p=1` : `https://5c86616fcc034a0014bd2569.mockapi.io/api/v1/${type}/${id}` 

        fetch(url)
            .then((response)=> {
                if(!response.ok){
                    throw Error(response.statusText)
                }

                dispatch(isBlogFetching(false))
                return response
            })
            .then((res)   => res.json())
            .then((items) => dispatch(fetchBlogSuccess(items)))
            .catch((err) => dispatch(fetchHasErrored(true)))
    }
}