import React from 'react'
import { Collapse } from 'reactstrap' 
import { NavigationLink } from '../../components/Nav/NavItem';
import PropTypes from 'prop-types'


export default function Links({isOpen, links}) {
    return <Collapse isOpen={isOpen} navbar className='justify-content-center order-2'>
        <ul className='navbar-nav' >
            {links && links.map((val, i) => <NavigationLink exact={ val.exact ? val.exact : false } to={val.to} key={i}>{val.name}</NavigationLink>)}
        </ul>
    </Collapse>
}
Links.propTypes={
    isOpen: PropTypes.bool.isRequired,
    links: PropTypes.arrayOf(PropTypes.shape({
        exact:PropTypes.bool,
        to:PropTypes.string.isRequired,
        name:PropTypes.string.isRequired
    }).isRequired)
}