const mapStateToProps = (state, ownProps) => ({
    isLoading: state.fetchedState.mainPage.isLoading ,
    hasError: state.fetchedState.mainPage.hasError,
    data: state.fetchedState.mainPage.data
})

const mapDispatchToProps = (dispatch) => ({
    fetchState: (endpoint) => dispatch(fetchState(endpoint))
})


export default connect(mapStateToProps, mapDispatchToProps)(HomePage)