import React from 'react'
import { NavItem } from 'reactstrap'
import { NavLink  } from 'react-router-dom' 
import './NavItem.css'

export function NavigationLink({ to, exact, name, ...other }) {
    return <NavItem >
            <NavLink
            to={to}
            className="nav-link link"// font_2"
            activeClassName='activee'
            exact={exact}
            {...other}>
            
            {other.children ? other.children : name}
        </NavLink>
        </NavItem>
}