const initialState = Imutable({mainPage:{},teachers:{}})
export const fetchedState = (state = initialState, action) => {
    switch (action.type) {
        case MAINPAGE_FETCH:
            return state.set(action.endpoint, { isLoading: action.isLoading, hasError: false})

        case MAINPAGE_FETCH_SUCCESS:
            return state.set(action.endpoint, { isLoading: false, hasError: false, data: action.payload})

            // state[action.endpoint] = { ...state[action.endpoint],  }
            // return state
        case MAINPAGE_FETCH_FAILED:
            return state.set(action.endpoint, { isLoading: false, hasError: true})

        default:
            return state
    }
}
