import React from 'react'
import { Route, Switch } from 'react-router'
import NavigationBar from '../containers/Nav/NavBar';
// import FetchState from '../hoc/FetchData/FetchState.jsx';
import HomePage from '../components/HomePage/HomePage.jsx';
import BlogFather from '../components/BlogFather/BlogFather';
import Blog from '../components/Blog/Blog';
import Footer from '../components/Footer/Footer';
import Teachers from '../components/Teachers/Teachers';
// import Footer from '../containers/Footer/Footer';

const routes = (
    <React.Fragment>
        <div className="page-wrapper">
            <div className="wrap">
                <NavigationBar />
                <Switch>
                    <Route exact path="/" component={HomePage} />
                    <Route path='/blog' exact render={props => (
                        <BlogFather type="blogss" {...props} />
                    )} />
                    <Route path='/blog/:id' render={props => (
                        <Blog type="blogss" {...props}/>
                    )}/>
                    {/* <Route path='/extracurricular' exact render={props => (
                        <BlogFather type="extracurricular" {...props} />
                    )} />
                    <Route path='/extracurricular/:id' render={props => (
                        <Blog type="extracurricular" {...props}/>
                    )}/> */}
                    <Route path='/(teachers|works|exam)' component={Teachers}/> 
                </Switch>
            </div>
        </div>
        <Footer/>
    </React.Fragment>
)

export default routes

