import React, { Component } from 'react'

export default class Highlight extends Component {

    generateHeader = (header) => {
        const regexp = /\*([a-zA-ZА-Яа-я ]*)\*/g
        let result, ar = []
        var lst = 0
        while (result = regexp.exec(header)) {
            ar.push({ st: 1, val: header.slice(lst, result.index) });
            ar.push({ st: 0, val: result[0].slice(1, -1) })
            lst = regexp.lastIndex
        }
        ar.push({ st: 1, val: header.slice(lst) });
        return ar
    };
    
    render() {
        const { MainType, DownType, children, ...props  } = this.props
        return (
            <MainType >
                {
                    this.generateHeader(children).map(({ st, val }, i) => {
                        if (st) {
                            return val
                        } else {
                            return <DownType {...props} key={i}>{val}</DownType>
                        }
                    })
                }
            </MainType>
        )
    }
}

Highlight.defaultProps = {
    MainType: 'h2',
    DownType: 'span',
    style: { color: '#F25A29' }
}
   