import React from 'react'
import { NavbarBrand, NavbarToggler } from 'reactstrap'

export default function Brand({toggle}) {
    return <div className="d-flex  order-0">
        <NavbarBrand style={{ color:'#F25A29'}}>Н.А.Ш.</NavbarBrand>
        <NavbarToggler onClick={toggle} />
    </div>
}